import { Component, h, Prop, Element, State } from '@stencil/core';
import { TIcon } from '../../../../components';

@Component({
  tag: 'dc-ui-header-flyout-nav-item',
  styleUrl: 'dc-ui-header-flyout-nav-item.scss',
  shadow: true,
})
export class DcUiHeaderFlyoutNavItem {
  @Element() el!: HTMLElement;
  @Prop() title!: string;
  @Prop() icon?: TIcon;
  @Prop() selected?: boolean;
  @State() isOpen: boolean = false;

  private hasSlotContent = false;

  componentWillLoad() {
    const slotElement = this.el.children;
    this.hasSlotContent = slotElement.length > 0;
  }

  render() {
    const hasNextLevel = this.hasSlotContent;

    return (
      <li
        class={{
          'flyout-nav-item': true,
          'has-next-level': hasNextLevel,
          selected: this.selected,
          'is-open': this.isOpen,
        }}>
        <button class="flyout-nav-item-trigger">
          {this.icon && (
            <dc-ui-icon
              name={this.icon}
              size="medium"
              class="item-icon"
            />
          )}
          <span class="item-title">{this.title}</span>
          {hasNextLevel && (
            <dc-ui-icon
              name="dc_forward_right"
              size="tiny"
              class="item-arrow"
            />
          )}
        </button>
        {hasNextLevel && (
          <div class="flyout-nav-item-content">
            <slot />
          </div>
        )}
      </li>
    );
  }
}
